.about-us {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  flex-basis: 100%;
}

.about-us-title {
  font-family: 'Rubik';
  font-size: 1.5rem;
  text-align: center;
  color: #099d8d;
  width: 100%;
  align-items: center;
}

.adalabers-item {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.img-container {
  text-align: center;
  width: 45%;
  height: 45%;
  margin: 0rem;
  padding: 0rem;
}

.adalaber-img {
  width: 100%;
  border-radius: 4px;
  border: solid 5px rgba(0, 0, 0, 0.08);
}

.information-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  margin: 0rem;
  padding: 0rem;
  width: 90%;
}

.adalabers-name {
  font-family: 'OpenSans';
  font-size: 1.25rem;
  text-align: center;
  width: 100%;
  margin-bottom: 0rem;
}

.adalaber-text {
  font-size: 1rem;
  line-height: 1.5rem;
  font-family: 'OpenSans';
  color: #54585a;
  text-align: center;
  margin: 1rem 0;
  width: 100%;
}

.about-us-icons {
 /* transform: scale(0.7); */
  margin-bottom: 2rem;
  color: #099d8d;
  line-height: 1.875rem;
  margin: 0rem;
}

.aside-list {
  margin-block-start: 0rem;
  margin-block-end: 0rem;
  padding: 0rem;
  margin-bottom: 2.5rem;
}

.aside-list a {
  font-size: 150%;
}

.about-us-icons-item {
  display: inline-block;
  margin: 0 1rem 0 0;
  list-style: none;
  padding-left: 2rem;
}

.about-us-icons-item a {
  text-decoration: none;
}

.fab {
  color: #099d8d;
}

.fas {
  color: #099d8d;
}

@media all and (min-width: 768px) {
  .about-us-container {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    margin: 0rem;
  }

  .adalabers-item {
    display: flex;
    flex-direction: row;
    margin-bottom: 3.5rem;
    align-items: flex-end;
    width: 100%;
  }

  .reverse {
    box-sizing: border-box;
    display: flex;
    flex-direction: row-reverse;
    margin-bottom: 3.5rem;
    width: 100%;
  }

  .img-container {
    box-sizing: border-box;
    width: 30%;
    margin-right: 1.5rem;
    margin-left: 1.5rem;
    height: auto;
  }

  .information-container {
    box-sizing: border-box;
    height: 100%;
    display: flex;
    flex-direction: column;
    width: 70%;
  }

  .adalabers-name {
    margin-left: 1.7rem;
    text-align: left;
  }

  .reverse .adalabers-name {
    margin-right: 3rem;
    text-align: right;
  }

  .reverse .adalaber-text {
    text-align: right;
    margin-right: 1rem;
    width: 100%;
  }

  .reverse .about-us-icons {
    text-align: right;
  }

  .adalaber-container-text {
    display: flex;
    width: 100%;
  }

  .adalaber-text {
    text-align: left;
    margin-left: 1rem;
  }

  .aside-list {
    padding-inline-start: 0rem;
    padding-inline-end: 0rem;
    display: inline-block;
    margin: 0rem;
    text-align: left;
  }

  .reverse .aside-list {
    text-align: right;
  }

  .about-us-icons-item {
    padding: 0 2rem 0 0;
  }

  .reverse .about-us-icons-item {
    padding: 0 0 0 2rem;
  }

  .about-us-icons {
    display: inline;
    align-self: flex-start;
    margin: 0 0 0 1rem;
  }

  .reverse .about-us-icons {
    align-self: flex-end;
    margin: 0;
  }
}

@media all and (min-width: 1200px) {
  .about-us-container {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    align-items: center;
    margin: auto;
    width: 70%;
  }

  .adalabers-item {
    width: 100%;
    align-self: center;
  }
}
